import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AceptarTerminosPage } from './aceptar-terminos.page';

const routes: Routes = [
  {
    path: '',
    component: AceptarTerminosPage,
  },
  {
    path: 'preview',
    loadChildren: () => import('src/app/pages/SST/preview/preview.module').then(m => m.PreviewPageModule),
  },
  {
    path: 'firmar',
    loadChildren: () => import('src/app/pages/SST/firmar/firmar.module').then(m => m.FirmarPageModule),
  },
  {
    path: 'firmar-si',
    loadChildren: () => import('src/app/pages/SST/firmar-si/firmar-si.module').then(m => m.FirmarSiPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AceptarTerminosPageRoutingModule {}
